<template>
  <div>
  <div id="app" style="text-align: center;">
    <tabla style="text-align:center;"></tabla>
  </div>
    <!--
      Aqu slo se debe poner cdigo que _siempre_ aparece, por ejemplo:
      Men de navegacin o footer que sale en todas las pginas.
      Lo ms seguro es que el componente tabla no deba estar aqu, sino
      a travs de una ruta? El index tb es una ruta! Ejemplo:
          { path: '/', component: Tabla  },
    -->
    <router-view   style="text-align: center;"   :career="$route.params.career" :courseCode="$route.params.courseCode"></router-view>
  </div>
</template>

<script>
// Los componentes son clases y por eso siempre deben importarse con la primera
// leta en mayscula. Debera ser: import Tabla.... As es ms fcil leer.
import tabla from "./tabla.vue";
export default {
  name:"app",
  components:{tabla},
  data(){
    return{msg:"hola"}
  },
  mounted () {
    this.$vuetify.init()
  }
}
</script>



